/* laptop style */
.navbar_container{
    z-index: 9;
}
@media screen and (max-width:2560px){
.navbar_container{
    background-color: #000000;
    transform: rotate(0deg);
    position:fixed;
    width:fit-content;
    height:100vh;
    padding:1rem;
    border-radius: 0 60px 60px 0;
    box-shadow: 0 0 20px .1px grey;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.navbar_container>ul{
    position: relative;
    list-style: none;
    height:50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
a{
    text-decoration: none;
    color:#878787;
}
.navbar_container>ul li:hover{
    cursor:pointer;
    transition: 1s;
    color:#00ffb9;
}

li.active>a{
    color:#00ffb9;
}
}

/* tablet style */

@media screen and (max-width: 768px) {
    .navbar_container{
        transform: rotate(0deg);
    }
    li.active>a{
        color:#00ffb9;
    }
}

/* mobile style */

@media screen and (max-width:426px){
.navbar_container{
  display: none;
}
}
