@media screen and (max-width:2560px){
.about_holder{
    display: flex;
    height: fit-content;
    font-family: 'Courier New', Courier, monospace;
}
.about_left{
    width:65vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.achieve{
    margin-left:5rem;
    font-family:'Courier New', Courier, monospace;
    line-height: 22px;
    padding:1rem;
    overflow: hidden;
}
.achieve>h3{
    text-decoration: none;
}
.about_holder_left2 >h2{
    margin-left: 4rem;
    padding:1rem
}
.card_holder{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: flex-end;
}
.acard{
    position: relative;
    margin-left:5rem;
    width:15rem;
    height:20rem;
    border-radius: 25px;
    overflow: hidden;

}
.cardimages{
    position: relative;
    padding:1rem;
    width:100%;
    height:60%;
}
.acard>h2{
    text-align: center;
    margin-top: 1rem;
}

.acard:hover{
    box-shadow: 0 0 30px 0 lightyellow;
    transition: 1s;
}
/* 3d-cube */
.about_right{
    width:35vw;
}
.cube_holder{
    position: relative;
    width:100%;
    height:100%;
    top:0;
    padding:17rem 10rem;
    margin-left: 0;
    right:0;
    overflow: hidden;
}
.cubespinner{
    animation: spincube 12s ease-in-out infinite;
    transform-style:preserve-3d;
    transform-origin:100px 100px 0;
    margin-left: calc(50%-100px);
}
.cubespinner div{
    position: absolute;
    width:200px;
    height:200px;
    border:1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0 lightyellow;
}
.face1{
    transform: translateZ(100px);
}
.face2{
    transform: rotateY(90deg) translateZ(100px);
}
.face3{
    transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
}
.face4{
    transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
}
.face5{
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
}
.face6{
    transform: rotateX(-90deg) translateZ(100px);
}
@keyframes spincube {
    from,to{
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16%{
        transform: rotateY(-90deg);
    }
    33%{
        transform: rotateY(-90deg) rotateZ(90deg);
    }
    50%{
        transform: rotateY(-180deg) rotateZ(90deg);
    }
    66%{
        transform:rotateY(-270deg) rotateZ(90deg);
    }
    83%{
        transform:rotateX(90deg) rotateZ(90deg)
    }
}
}
@media screen and (max-width:768px) {
    .about_left {
        height: fit-content;
        justify-content: space-around;
    }  
    .achieve {
        line-height: 20px;
    } 
    .card_holder {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-content: flex-end;
    }
    .acard {
        position: relative;
        margin-left: 5rem;
        margin-top: 1.5rem;
        width: 11rem;
        height: 12rem;
    }
    .cardimages {
        padding: 0.2rem;
    }
    .cube_holder {
        position: relative;
        width: 82%;
        height: 89%;
        display: flex;
        padding:0rem;
        margin-left: 43px;
        overflow:visible;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: flex-start;
    }
    .cubespinner div {
        position: absolute;
        width: 150px;
        height: 150px;
    }
    .face1{
        transform: translateZ(75px);
    }
    .face2{
        transform: rotateY(90deg) translateZ(75px);
    }
    .face3{
        transform: rotateY(90deg) rotateX(90deg) translateZ(75px);
    }
    .face4{
        transform: rotateY(180deg) rotateZ(90deg) translateZ(75px);
    }
    .face5{
        transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px);
    }
    .face6{
        transform: rotateX(-90deg) translateZ(75px);
    }
}

@media screen and (max-width:426px) {
    .about_holder {
        width: 100vw;
        display: flex;
        flex-direction: column;
        margin-bottom: 15rem;
    }
    .about_left {
        width: 100vw;
    }
    .achieve {
        margin-left: 0;
        line-height: 20px;
    }
    .achieve h3{
        text-indent: 1rem;
    }
    .card_holder {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .about_holder_left2{
        width: 100vw;
    }
    .card_holder {
        display: flex;
        flex-direction: row;
    }
    .acard {
        position: relative;
        margin-left: 1rem;
        margin-top: 1.5rem;
        width: 11rem;
        height: 12rem;
    }
    .cube_holder {
        align-items: center;
        margin-top: 5rem;
    }
}