body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
background-color:  #000000;
color: #00ffb9;
}

body::-webkit-scrollbar{
    width:.2rem;
}
body::-webkit-scrollbar-thumb{
    background-color:#00ffb9;
}
body::-webkit-scrollbar-track{
    background-color:#878787 ;
}

/* laptop style */
.navbar_container{
    z-index: 9;
}
@media screen and (max-width:2560px){
.navbar_container{
    background-color: #000000;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    position:fixed;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    height:100vh;
    padding:1rem;
    border-radius: 0 60px 60px 0;
    box-shadow: 0 0 20px .1px grey;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.navbar_container>ul{
    position: relative;
    list-style: none;
    height:50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
a{
    text-decoration: none;
    color:#878787;
}
.navbar_container>ul li:hover{
    cursor:pointer;
    transition: 1s;
    color:#00ffb9;
}

li.active>a{
    color:#00ffb9;
}
}

/* tablet style */

@media screen and (max-width: 768px) {
    .navbar_container{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    li.active>a{
        color:#00ffb9;
    }
}

/* mobile style */

@media screen and (max-width:426px){
.navbar_container{
  display: none;
}
}

@media screen and (max-width:2560px) {
.home_container{
    display: flex;
}
/* details */
.left{
    /* border:2px solid green; */
    height:100vh;
    width:58vw;
    margin-left: 4vw;
    display: flex;
    align-items: center;
    /* text-align: left; */
}
.intro{
    /* border:2px solid red; */
    font-family:'Courier New', Courier, monospace;
    padding:1rem 1rem 1rem 5rem;
}
.intro > h1{
    font-size: 1.3rem;
    color:#ffffff;
}
.intro > p{
    font-size:1rem;
    color:#878787;
}

/* cards */
.right{
    /* border:2px solid yellow; */
    height:100vh;
    width:38vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    height:25rem;
    width:19rem;
}
.card{
    position: relative;
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
.front,.back{
    position: absolute;
    display: flex;
    align-items: center;
    width:100%;
    height:100%;
    inset: 4;
    border:1px solid rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transition: 1s;
    box-shadow:20px 20px 50px rgba(0,0,0,0.5);
}
.front{
    color:white;
    display: flex;
    align-items: center;
    justify-self: center;
    -webkit-animation: rotater 1s linear 1s 3;
            animation: rotater 1s linear 1s 3;
}
.front svg{
    background: none;
    stroke: #00ffb9;
    stroke-width: 2px;
    width:300px;
    height:181px;
}
.back{
    -webkit-transform:perspective(800px) rotateY(180deg);
            transform:perspective(800px) rotateY(180deg);
    padding:2.5rem;
        
}
@-webkit-keyframes rotater {

    0%{
        -webkit-transform:perspective(800px) rotateY(0deg);
                transform:perspective(800px) rotateY(0deg);
        
    }
    100%{
        -webkit-transform:perspective(800px)  rotateY(-20deg);
                transform:perspective(800px)  rotateY(-20deg);
    }
}
@keyframes rotater {

    0%{
        -webkit-transform:perspective(800px) rotateY(0deg);
                transform:perspective(800px) rotateY(0deg);
        
    }
    100%{
        -webkit-transform:perspective(800px)  rotateY(-20deg);
                transform:perspective(800px)  rotateY(-20deg);
    }
}
.texter{
    position: relative;
    color:#00ffb9;
    background: none;
    margin:0;
    font-family:'Courier New', Courier, monospace;
    line-height: 21px;
    text-align: left;
}
.texter::after{
    content:"_";
    -webkit-animation: blink .3s linear infinite;
            animation: blink .3s linear infinite;
}
.blinker{
    justify-content: flex-end;
    background: none;
    border-radius: 2px;
    -webkit-animation: blink .3s linear infinite;
            animation: blink .3s linear infinite;
    opacity:0;
}
@-webkit-keyframes blink {
100%{
    opacity: 1;
}    
}
@keyframes blink {
100%{
    opacity: 1;
}    
}
.card:hover .back{
    transition: 1s;
    -webkit-transform:perspective(800px)  rotateY(0deg);
            transform:perspective(800px)  rotateY(0deg);
}
.card:hover .front{
    transition: 1s;
    -webkit-transform:perspective(800px)  rotateY(-180deg);
            transform:perspective(800px)  rotateY(-180deg)
}
.card:hover{
    transition: 1s;
    -webkit-transform:perspective(800px)  rotate(0deg);
            transform:perspective(800px)  rotate(0deg)
}
}
@media screen and (max-width: 768px) {
    .home_container{
        /* background-color: chartreuse; */
        width:100vw;
