        justify-content: center;
    }
    .container{
        position: relative;
        width: 18rem;
        height: 25rem;
    }
    
    .card{
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    .front{
        display: none;
    }
    .back{
        -webkit-transform:perspective(800px)  rotateY(0deg);
                transform:perspective(800px)  rotateY(0deg);
        padding: 1.5rem;
    }
    .texter {
        line-height: 20px;
    }
}

