/* 3d-cube */
.about_right{
    width:35vw;
}
.cube_holder{
    position: relative;
    width:100%;
    height:100%;
    top:0;
    padding:17rem 10rem;
    margin-left: 0;
    right:0;
    overflow: hidden;
}
.cubespinner{
    -webkit-animation: spincube 12s ease-in-out infinite;
            animation: spincube 12s ease-in-out infinite;
    -webkit-transform-style:preserve-3d;
            transform-style:preserve-3d;
    -webkit-transform-origin:100px 100px 0;
            transform-origin:100px 100px 0;
    margin-left: calc(50%-100px);
}
.cubespinner div{
    position: absolute;
    width:200px;
    height:200px;
    border:1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0 lightyellow;
}
.face1{
    -webkit-transform: translateZ(100px);
            transform: translateZ(100px);
}
.face2{
    -webkit-transform: rotateY(90deg) translateZ(100px);
            transform: rotateY(90deg) translateZ(100px);
}
.face3{
    -webkit-transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
            transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
}
.face4{
    -webkit-transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
            transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
}
.face5{
    -webkit-transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
            transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
}
.face6{
    -webkit-transform: rotateX(-90deg) translateZ(100px);
            transform: rotateX(-90deg) translateZ(100px);
}
@-webkit-keyframes spincube {
    from,to{
        -webkit-transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
                transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16%{
        -webkit-transform: rotateY(-90deg);
                transform: rotateY(-90deg);
    }
    33%{
        -webkit-transform: rotateY(-90deg) rotateZ(90deg);
                transform: rotateY(-90deg) rotateZ(90deg);
    }
    50%{
        -webkit-transform: rotateY(-180deg) rotateZ(90deg);
                transform: rotateY(-180deg) rotateZ(90deg);
    }
    66%{
        -webkit-transform:rotateY(-270deg) rotateZ(90deg);
                transform:rotateY(-270deg) rotateZ(90deg);
    }
    83%{
        -webkit-transform:rotateX(90deg) rotateZ(90deg);
                transform:rotateX(90deg) rotateZ(90deg)
    }
}
@keyframes spincube {
    from,to{
        -webkit-transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
                transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16%{
        -webkit-transform: rotateY(-90deg);
                transform: rotateY(-90deg);
    }
    33%{
        -webkit-transform: rotateY(-90deg) rotateZ(90deg);
                transform: rotateY(-90deg) rotateZ(90deg);
    }
    50%{
        -webkit-transform: rotateY(-180deg) rotateZ(90deg);
                transform: rotateY(-180deg) rotateZ(90deg);
    }
    66%{
        -webkit-transform:rotateY(-270deg) rotateZ(90deg);
                transform:rotateY(-270deg) rotateZ(90deg);
    }
    83%{
        -webkit-transform:rotateX(90deg) rotateZ(90deg);
                transform:rotateX(90deg) rotateZ(90deg)
    }
}
}
