@media screen and (max-width:2560px) {
.contact_holder{
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Courier New', Courier, monospace;
}
.contact_card{
    width:60vw;
    height:30vh;
    border-radius: 25px;
    background-image: url('../../Image/contact_bpic.png');
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact_card > div{
    display: flex;
    margin-top: 2rem;
    align-items: center;
}
.cnt_icn{
    font-size: 35px;
    color:#878787;
    margin:0 1rem;
}
.mailer{
    text-decoration: none;
    color:#00ffb9;
}
.contact_card h3{
    text-decoration: none;
}
}

@media screen and (max-width:768px){
    .contact_card {
        width: 75vw;
        font-size: 12px;
    }
    .contact_card h3{
        text-decoration: none;
    }
}
@media screen and (max-width:426px) {
    .contact_holder {
        width: 90vw;
    }
    .contact_card {
        width: 85vw;
        height: 90%;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .contact_card > div {
        display: flex;
        margin-top: 2rem;
        flex-direction: column;
        align-items: center;
    }
    .contact_card h3{
        text-decoration: none;
    }
}