@media screen and (max-width:2560px) {
    .experience_holder{
        display: flex;
        font-family: 'Courier New', Courier, monospace;
        height:100vh;
    }
    .workimage{
        width: 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.work_image{
    width:45vw;
}
.workdetails{
    color:#00ffb9;
    width: 48vw;
    line-height: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.work1>ul,.internship1>ul{
    list-style:decimal;
    text-indent: 1rem;
}
h3{
    text-decoration: underline;
}
.work1 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.internship1 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
}

@media screen and (max-width:768px) {
    .workdetails {
        color: #00ffb9;
        width: 49vw;
        height: 100vh;
        line-height: 14px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
    .work1 {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .internship1 {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

}

@media screen and (max-width:426px) {
    .experience_holder {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: fit-content;
        flex-wrap: nowrap;
    }
    .workimage {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .work_image {
        width: 90vw;
    }
    .workdetails {
        width:90vw;
        height: fit-content;        
    }
    .work1>ul, .internship1>ul {
        list-style: none;
        text-indent: 1rem;
    }
}