*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
background-color:  #000000;
color: #00ffb9;
}

body::-webkit-scrollbar{
    width:.2rem;
}
body::-webkit-scrollbar-thumb{
    background-color:#00ffb9;
}
body::-webkit-scrollbar-track{
    background-color:#878787 ;
}
