@media screen and (max-width:2560px) {
.home_container{
    display: flex;
}
/* details */
.left{
    /* border:2px solid green; */
    height:100vh;
    width:58vw;
    margin-left: 4vw;
    display: flex;
    align-items: center;
    /* text-align: left; */
}
.intro{
    /* border:2px solid red; */
    font-family:'Courier New', Courier, monospace;
    padding:1rem 1rem 1rem 5rem;
}
.intro > h1{
    font-size: 1.3rem;
    color:#ffffff;
}
.intro > p{
    font-size:1rem;
    color:#878787;
}

/* cards */
.right{
    /* border:2px solid yellow; */
    height:100vh;
    width:38vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    height:25rem;
    width:19rem;
}
.card{
    position: relative;
    width: 100%;
    height: 100%;
    transform: rotate(10deg);
}
.front,.back{
    position: absolute;
    display: flex;
    align-items: center;
    width:100%;
    height:100%;
    inset: 4;
    border:1px solid rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transition: 1s;
    box-shadow:20px 20px 50px rgba(0,0,0,0.5);
}
.front{
    color:white;
    display: flex;
    align-items: center;
    justify-self: center;
    animation: rotater 1s linear 1s 3;
}
.front svg{
    background: none;
    stroke: #00ffb9;
    stroke-width: 2px;
    width:300px;
    height:181px;
}
.back{
    transform:perspective(800px) rotateY(180deg);
    padding:2.5rem;
        
}
@keyframes rotater {

    0%{
        transform:perspective(800px) rotateY(0deg);
        
    }
    100%{
        transform:perspective(800px)  rotateY(-20deg);
    }
}
.texter{
    position: relative;
    color:#00ffb9;
    background: none;
    margin:0;
    font-family:'Courier New', Courier, monospace;
    line-height: 21px;
    text-align: left;
}
.texter::after{
    content:"_";
    animation: blink .3s linear infinite;
}
.blinker{
    justify-content: flex-end;
    background: none;
    border-radius: 2px;
    animation: blink .3s linear infinite;
    opacity:0;
}
@keyframes blink {
100%{
    opacity: 1;
}    
}
.card:hover .back{
    transition: 1s;
    transform:perspective(800px)  rotateY(0deg);
}
.card:hover .front{
    transition: 1s;
    transform:perspective(800px)  rotateY(-180deg)
}
.card:hover{
    transition: 1s;
    transform:perspective(800px)  rotate(0deg)
}
}
@media screen and (max-width: 768px) {
    .home_container{
        /* background-color: chartreuse; */
        width:100vw;
        height:100vh;
    
    }
    .container{
        width: 14rem;
        height: 22rem;
    }
    .card{
        width: 100%;
        height: 100%;
    }
    .front svg{
        height:182px;
    }
    .back{
        padding:.5rem;
    }
    .texter{
        line-height: 16px;
    }
}
@media screen and (max-width: 426px) {
    .home_container{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .left, .right{
        width: 100%;
    }
    .right{
        display: flex;
        justify-content: center;
    }
    .container{
        position: relative;
        width: 18rem;
        height: 25rem;
    }
    
    .card{
        transform: rotate(0);
    }
    .front{
        display: none;
    }
    .back{
        transform:perspective(800px)  rotateY(0deg);
        padding: 1.5rem;
    }
    .texter {
        line-height: 20px;
    }
}
